import request from './request'

export const loginByUsername = (params) => {
    return request({
        url: '/Login/doLogin',
        method: 'post',
        data: params
    })
}

export const logout = (params) => {
    return request({
        url: '/Login/loginOut',
        method: 'post',
        data: params
    })
}

export const getUserInfo = (params) => {
    return request({
        url: '/Login/login',
        method: 'post',
        params
    })
}