import { loginByUsername, logout } from '@/api/login.js'
import { Message } from 'element-ui'

const user = {
    state: {
        username: '',
        userinfo: {},
        token: localStorage.getItem('token'),
        avatar: '',
        roles: []
    },

    mutations: {
        set_token: (state, token) => {
            state.token = token
        },
        set_username: (state, username) => {
            state.username = username
        },
        set_userinfo: (state, userinfo) => {
            state.userinfo = userinfo
        },
        set_avatar: (state, avatar) => {
            state.avatar = avatar
        },
        set_roles: (state, roles) => {
            state.roles = roles
        }
    },

    actions: {
        // 用户名登录
        LoginByUsername({ commit }, login_form) {
            return new Promise((resolve, reject) => {
                /*const data = {
                    token: 'asudfgdsgfjkgsdj'
                }
                commit('set_token', data.token)

                localStorage.setItem('token',data.token)
                resolve()*/
                /*loginByUsername({}).then(res => {

                    alert(888)
                })*/
                loginByUsername({user_name: login_form.user_name, user_pass: login_form.user_pass}).then(res => {
                    if(res.code == 0){
                        commit('set_token', res.data.token)
                        commit('set_userinfo', res.data)
                        localStorage.setItem('token',res.data.token)
                        localStorage.setItem('username',res.data.user_name)
                        resolve()
                    }else{
                        Message.error(res.msg);
                    }
                    
                }).catch(error => {
                    console.log(error)
                    reject(error)
                })
            })
        },

        // 获取用户信息
        GetUserInfo({ commit, state }) {
            return new Promise((resolve, reject) => {
                const data = {
                    roles: ['admin'],
                    username: 'admin'
                }
                commit('set_roles', data.roles)
                // commit('set_username', data.username)
                commit('set_username', localStorage.getItem('username'))
                resolve(data)
                /*getUserInfo(state.token).then(response => {
                    const data = response.data
                    commit('set_roles', data.roles)
                    commit('set_username', data.name)
                    resolve(response)
                }).catch(error => {
                    reject(error)
                })*/
            })
        },

        // 登出
        LogOut({ commit, state }) {
            return new Promise((resolve, reject) => {
                /*commit('set_token', '')
                commit('set_roles', [])
                localStorage.clear()
                resolve()*/
                logout({}).then((res) => {
                    if(res.code == 0){
                        commit('set_token', '')
                        commit('set_roles', [])
                        commit('set_userinfo', {})
                        localStorage.clear()
                        resolve()
                    }
                }).catch(error => {
                    reject(error)
                })
            })
        },

        // 前端 登出
        FedLogOut({ commit }) {
            return new Promise(resolve => {
                commit('set_token', '')
                localStorage.clear()
                resolve()
            })
        },

        // 动态修改权限
        ChangeRoles({ commit }, role) {
            return new Promise(resolve => {
                commit('set_token', role)
                setToken(role)
                getUserInfo(role).then(response => {
                    const data = response.data
                    commit('set_roles', data.roles)
                    commit('set_username', data.name)
                    commit('set_avatar', data.avatar)
                    resolve()
                })
            })
        }
    }
}

export default user