import axios from 'axios'
import router from '@/router';
import Qs from 'qs'
import CryptoJS from 'crypto-js'
import { Message } from 'element-ui'
import store from '@/store'
import { requestUrl } from './config'
import JsEncrypt from 'jsencrypt'


const service = axios.create({
    // baseURL: process.env.NODE_ENV == 'production' ? 'http://lgcustomerapidev.aerohuanyou.com' : '/yue',
    baseURL: requestUrl,
    transformRequest: [function (data) {
        return Qs.stringify(data);
    }],
    /*paramsSerializer: function(params) {
        return Qs.stringify(params, {arrayFormat: 'indices'})
    }*/
})

service.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
/**
 * 获取token
 * @return {[string]} [返回token字符串]
 */
function getToken() { 
    let token = localStorage.getItem('token');
    if(token == null){
        router.push({path: '/login'})
    }else{
        return token || '';
    }
};

/**
 * RSA加密
 * @return {[string]} [返回加密后字符串]
 */
function encryptRSA(word){
    let jse = new JsEncrypt()
    jse.setPublicKey(`-----BEGIN PUBLIC KEY-----
MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCSykUWpOo7fkd54h2Y2MDaJw5q
69fNLKFMETQFjuSSY6qlf9mdAQaTh4qVny/EOaaB2Q+a6QIO2X+BFFjrk+yQoYkd
i0mPhJB7nSkkQ2YE8KOh3pzpSpemsfvXJJq3beWL0+n4OkBfTagS73n82aFwaV5h
ZfCI4VY1IRAabL63YQIDAQAB
-----END PUBLIC KEY-----`)
    let encrypted = jse.encrypt(word)
    return encrypted
}


/**
 * AES加密
 * @return {[string]} [返回加密后字符串]
 */
function encryptAES(word){
    let key = 'eIa@yb^vpG4Kovtr';
    key = CryptoJS.enc.Utf8.parse(key);
    let iv = "eIa@yb^vpG4Kovtr";
    iv = CryptoJS.enc.Utf8.parse(iv);
    let encrypted = CryptoJS.AES.encrypt(word, key, {
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7 
    }).toString();
    return encrypted;
}

service.interceptors.request.use(config => {
    let token = config.url.indexOf('doLogin') > -1 ? '' : getToken();
    if(token != null){
        config.headers['X-Token'] = token;
        localStorage.setItem("token", token);
    }
    //login  加密开始
    if( config.url.indexOf('doLogin') > -1){
        let str=encryptRSA(JSON.stringify(config.data));
        config.data={}
        config.data.data=str;
    }
    return config
},function(error){
    return Promise.reject(error)
})

service.interceptors.response.use(response => {
    if(response.data.code != 0){
        if (response.data.code == 100000113 || response.data.code == 100000114){
            localStorage.clear();
            router.replace({
                path: 'login',
                query: {redirect: router.currentRoute.fullPath}
            })
        }

        return response.data;
        
        /*Message.error(response.data.msg);
        return new Promise(() => {});*/
    }else{
        return response.data;
    }
    
    
}, function (error) {
    return Promise.reject(error)
})

export default service